.nested-list__drag {
    align-items: center;
    background-color: #000000;
    cursor: move;
    display: flex;
    height: 40px;
    justify-content: center;
    left: 0;
    position: absolute;
    top: 50%;
    width: 20px;
    transform: translate(-50%, -50%);
}

.nested-list__list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.nested-list__drag,
.nested-list__item-inner {
    border-radius: 10px;
}

.nested-list__item {
    padding-bottom: 20px;
}

.nested-list__item-inner {
    background-color: #ffffff;
    border: 1px solid #000000;
    padding: 20px;
    position: relative;
}

.nested-list__item--hover .nested-list__item-inner,
.nested-list__item--dragging .nested-list__item-inner {
    background-color: rgba(255, 255, 255, 0.7);
    border-style: dashed;
}

.nested-list__item--hover .nested-list__drag,
.nested-list__item--hover .nested-list__title,
.nested-list__item--dragging .nested-list__drag,
.nested-list__item--dragging .nested-list__title {
    opacity: 0;
}

.nested-list__item .nested-list__drag {
    background-color: #ffffff;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
}

.nested-list__item path {
    fill: #000000;
}
